@use '../../style/importer' as v;

.main {
    @include v.flex-column;
    flex-grow: 1;

    .section {
        @include v.flex-column;
        position: relative;
        flex-grow: 1;
        gap: 20px;
        margin-top: 30px;
    }

    @media (width <= 420px) {
        padding-top: 0;
    }
}
